import React from "react";
import Link from "gatsby-link";
import Preview from '../../components/Preview'
import Helmet from "react-helmet";
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import faAngleLeft from '@fortawesome/fontawesome-free-solid/faAngleLeft';

export default function Abs({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">

      <Helmet title={`Группа мышц - Пресс`} />
      <div className="page-top-banner">
        <h2 className="page-heading" style={absStyle}> Упражнения для пресса</h2>
        <Link to="/" className="page-btn-back"><FontAwesomeIcon icon={faAngleLeft} className="preview-btn-icon" /> Все упражнения</Link>
      </div>
      {posts.filter(post => post.node.frontmatter.filterby === 'abs').map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const absStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/otfwgbp8f/abs.jpg)',
  backgroundPosition: "center"
};

export const pageQuery = graphql`
  query AbsQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



